import { Avatar } from '@material-ui/core';
import React from 'react';
import "./Post.css";
import PostBar from "./PostBar";

function Post({ profilePic, key, image,video, username, timestamp, message, like, comment, share, totlik, totsha, totcom, posid, comtime, comarr, comuser, compic }) {
    
    var vidsty = { display: 'none'};
    var imgsty = { display: 'none'};
	if(video != "")
	{
			vidsty = { display: 'block'};
	}
	if(image != "")
	{
			imgsty = { display: 'block'};
	}
    return (
        <div className="post">
            <div className="post_top">
                <Avatar src={profilePic}
                className="post_avatar" />
                <div className="post_topInfo">
                    <h6> {username} </h6>
                    <p>{timestamp}</p>
                </div>
            </div>

            <div className="post_bottom">
                <p> {message} </p>
            </div>

            <div className="post_image" style={imgsty}>
                <img src={image} alt="" id="myImg"/>
            </div>
			
            <div className="post_video" style={vidsty}>
                <video id="myVid" controls>
				<source src={video} />
				</video>
            </div>

            <PostBar 
                like = {like} 
                comment = {comment}  
                share = {share} 
                posid = {posid}
                totlik={totlik}
                totcom={totcom}
                totsha={totsha}
                comtime={posid}
                comarr={comarr}
                comuser={comuser}
                compic={compic}
            />
        </div>
    )
}

export default Post
